import PouchDB from "pouchdb";
import { DatabaseUtils } from "../Utils/DatabaseUtils";
import { UserMap } from "./usermapmodel";

/**
 * Modle de donne pour une zone.
 *
 * Relations:
 *   Zone -|---N- UserMap
 */
export class Zone {
  // Attributes
  _id: string; // nom de la zone

  // Relations
  maps: Map<string, UserMap>;

  constructor(name: string) {
    this._id = name;
    this.maps = new Map<string, UserMap>();
  }

  /** @DEPRECATED */
  /** Sauvegarde un objet Zone dans un document NoSQL */
  async save() {
    var db = new PouchDB(DatabaseUtils.getProdDB());
    var doc = null;
    try {
      doc = await db.post({
        _id: this._id,
        maps: this.maps,
      });
    } catch (err) {
      console.log(err);
    } finally {
      await db.close();
    }

    return doc;
  }

  /** @DEPRECATED */
  /** Va chercher un objet Zone ainsi que toutes les classes imbriques dans un document NoSQL */
  static async get(id: string) {
    var db = new PouchDB(DatabaseUtils.getProdDB());

    var doc: Zone;
    try {
      doc = await db.get<Zone>(id);
    } catch (err) {
      return err;
    } finally {
      await db.close();
    }

    let z = new Zone(doc!["_id"]);
    z.maps = doc!.maps;

    return z;
  }

  toJson() {
    let a: any[] = [];
    let jsonObject: any = {};
    jsonObject["_id"] = this._id;
    this.maps.forEach((value: UserMap) => {
      a.push(value.toJson());
    });
    jsonObject["maps"] = a;

    return jsonObject;
  }
}
