/**
 * Les types de donnes qui sont possible d'utiliser.
 */
export enum DataType {
  video = "video",
  image = "image",
  texte = "texte",
}

export function getDataType(x: string): DataType {
  return (DataType as any)[x];
}

/**
 * Modle de donne pour les fichiers et le texte associ  un point d'intrt.
 *
 * Relations:
 *   InterestPointData -N---|-InterestPoint
 */
export class InterestPointData {
  // Attributes
  type: DataType;
  data: string;

  constructor(type: DataType, data: string) {
    this.type = type;
    this.data = data;
  }
}
