import { CompleteModel } from "../models/completemodel";
import { DatabaseUtils } from "../Utils/DatabaseUtils";

/**
 * Mthode pour confirmer la mise  jour de l'application selon le preview.
 *
 * @param excel Le fichier excel  envoyer sur le serveur..
 * @param model Le model  envoyer sur le serveur.
 * @returns vrai si les informations ont t envoyes au serveur.
 */
export async function sendForUpdate(
  excel: File,
  model: CompleteModel
): Promise<boolean> {
  var error = await DatabaseUtils.sendModelToServer(excel, model);
  if (error === null) {
    return Promise.resolve(true);
  } else {
    console.log("Erreur lors de l'envoi sur le serveur. " + error);
    return Promise.resolve(false);
  }
}

/**
 * Mthode pour pour detruire la bd, annuler.
 *
 * @returns vrai si les informations ont t supprimes.
 */
export async function deleteLocalDB(): Promise<boolean> {
  var error = await DatabaseUtils.destroy();
  if (error === null) {
    return Promise.resolve(true);
  }
  console.warn(" Erreur lors de la destruction de la BD: " + error);
  return Promise.resolve(false);
}
