import { DatabaseUtils } from "../Utils/DatabaseUtils";

/**
 *
 * Mthode pour tlcharger le dernier Excel utilis pour mettre  jour l'aplication.
 */
export async function downloadFile() {
  try {
    var a = document.createElement("a");
    document.body.appendChild(a);
    a.setAttribute("style", "display: none;");
    a.href = "http://sentiersest.web810.discountasp.net/downloadFile";
    a.click();
    window.URL.revokeObjectURL(
      "http://sentiersest.web810.discountasp.net/downloadFile"
    );
    return true;
  } catch (error) {
    console.log(error);
    return false;
  }
}

/**
 * Mthode pour avoir la date du dernier update.
 */
export async function getLastUpdatedDate() {
  return await DatabaseUtils.getDateFromAppStatus();
}
