import { Zone } from "./zonemodel";

/**
 * Contient l'ensemble du modle de donnes dans un objet typescript
 */
export class CompleteModel {
  zones: Map<string, Zone>;

  constructor() {
    this.zones = new Map<string, Zone>();
  }

  toJson() {
    let jsonObject: any = {};
    let a: any[] = [];
    this.zones.forEach((value: Zone) => {
      a.push(value.toJson());
    });
    jsonObject["zones"] = a;
    return jsonObject;
  }
}