import PouchDB from "pouchdb";
import { CompleteModel } from "../models/completemodel";

export class DatabaseUtils {
  /**
   * Retourne le nom  utiliser pour instancier une base de donnes. Doit tre un nom sans slash '/': sur Windows
   * PouchDB peut-tre instanci  n'importe quel endroit dans le projet mais le constructeur utilisant un "path"
   * choue sur Linux. Par soucis d'uniformit nous n'utiliserons pas de "path" dans le constructeur de la DB.
   */
  public static getProdDB() {
    return "db_prod";
  }
  public static getIntegrationTestDB() {
    return "db_integrationtest";
  }

  /** Va chercher tous les json ainsi que toutes les classes imbriques du fichier NoSQL et l'affiche  l'cran */
  static async getAllDocuments() {
    var db = new PouchDB(DatabaseUtils.getProdDB());

    var options = {
      include_docs: true,
      attachments: true,
    };

    var doc;
    try {
      doc = await db.allDocs(options);
    } catch (err) {
      console.log(err);
    } finally {
      await db.close();
    }

    return doc;
  }

  /** Dtruit la BD prod */
  static async destroy() {
    var db = new PouchDB(DatabaseUtils.getProdDB());

    var e = null;
    try {
      await db.destroy();
    } catch (err) {
      e = err;
    }

    return e;
  }

  /** Envoie le model et le excel servant  mettre  jour sur le serveur */
  static async sendModelToServer(excel: File, model: CompleteModel) {
    var e = null;
    try {
      console.log(model.toJson());
      // envoyer le json
      var response = await DatabaseUtils.postRequestToServer(
        JSON.stringify(model.toJson()),
        false
      );
      if (response === 200) {
        // envoyer le excel
        response = await DatabaseUtils.postRequestToServer(excel, true);
        if (response === 200) {
          return e;
        }
      }
      return " Le statut de la rponse HTTP rone est : " + response;
    } catch (err) {
      e = err;
      console.log(e);
    }

    return e;
  }

  /** Requete POST pour envoyer les informations au serveur */
  static async postRequestToServer(toSend: any, isExcel: boolean) {
    var formdata = toSend;
    var requestHeaders;

    var destinationUri =
      "http://sentiersest.web810.discountasp.net/updateAppContent";

    if (isExcel) {
      formdata = new FormData();
      formdata.append("spreadsheet", toSend);
      destinationUri = "http://sentiersest.web810.discountasp.net/uploadFile";
      requestHeaders = {
        Accept: "*/*",
      };
    } else {
      requestHeaders = {
        "Content-Type": "application/json",
      };
    }

    var requestOptions: RequestInit = {
      method: "POST",
      headers: requestHeaders,
      body: formdata,
    };
    try {
      console.log(formdata);
      var response = await fetch(destinationUri, requestOptions);
      return response.status;
    } catch (error) {
      throw error;
    }
  }

  static async getDateFromAppStatus() {
    try {
      var requestOptions = {
        method: "GET",
      };
      var response = await fetch(
        "http://sentiersest.web810.discountasp.net/getStatus",
        requestOptions
      );
      return response.text();
    } catch (error) {
      throw error;
    }
  }
}
